@import url(https://fonts.googleapis.com/css?family=Karla|Fira+Mono);
h1, h2, h3 {
  font-family: 'Fira Mono', sans-serif;
  font-weight: 900;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Fira Mono', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 400;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Container {
  position: relative;
  text-align: center;
  color: white;
  min-width: 1200px;
}

.HeaderImage {
  width: 100%;
  height: auto;
  min-width: 1000px;
}

.Header {
  position: fixed;
  z-index: 1;
  width: 100%;
  height: 64px;
  min-width: 1000px;
  background: #000000ff;
}

.AppMenu {
  line-height: 64px;
  background: #00000000;
  width: 50vw;
  min-width: 800px;
  height: 100%;
  margin-left: auto;
  margin-right: auto;
  display: block;
  flex-direction: column;
}

.ContentBlock {
  background: #fff;
  width: 50vw;
  min-width: 800px;
  height: auto;
  margin-left: auto;
  margin-right: auto;
  margin-top: 50px;
  padding: 40px;
  display: block;
  flex-direction: column;
}

.HomeViewTitleName {
  width: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  color: #000;
}

.App-link {
  color: #61dafb;
}

.Centered {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.HomeTextContent {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.logo {
  width: 120px;
  height: 31px;
  background: rgba(255,255,255,.2);
  margin: 16px 28px 16px 0;
  float: left;
}

